<template>
  <div class="panel">
    <el-form style="90%" :model="RePwd" :rules="rules2" ref="RePwd"  class="demo-ruleForm">
      <el-form-item prop="userPwd">
        <el-input type="password" v-model="RePwd.userPwd" size="large" placeholder="旧密码" ></el-input>
      </el-form-item>
      <el-form-item prop="pass">
        <el-input type="password" v-model="RePwd.pass" size="large" auto-complete="off"  placeholder="新密码"></el-input>
      </el-form-item>
      <el-form-item prop="newPwd">
        <el-input type="password" v-model="RePwd.newPwd" size="large" auto-complete="off"  placeholder="重复密码"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button style="width:47%" size="large" type="primary" @click="submitForm('RePwd')">提交</el-button>
        <el-button style="width:48%" size="large" @click="resetForm('RePwd')">重置</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  export default {
    data() {
      var validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入新密码'));
        } else {
          if (this.RePwd.newPwd !== '') {
            this.$refs.RePwd.validateField('newPwd');
          }
          callback();
        }
      };
      var validatePass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请再次输入密码'));
        } else if (value !== this.RePwd.pass) {
          callback(new Error('两次输入密码不一致!'));
        } else {
          callback();
        }
      };
      return {
        RePwd: {
          userId: '',
          pass: '',
          newPwd: '',
          userPwd: ''
        },
        rules2: {
          pass: [
            { validator: validatePass, trigger: 'blur' },
            { min: 6, max: 16, message: '密码 6-20 位之间！', trigger: 'blur' }
          ],
          newPwd: [
            { validator: validatePass2, trigger: 'blur' },
            { min: 6, max: 16, message: '密码 6-20 位之间！', trigger: 'blur' }
          ],
          userPwd: [
            { required: true, message: '请输入密码！', trigger: 'blur' },
            { min: 6, max: 16, message: '密码 6-20 位之间！', trigger: 'blur' }
          ]
        }
      };
    },
    methods: {
      submitForm(formName) {
        var _this = this
        var user = JSON.parse(sessionStorage.getItem('BIGDATA_PLATFORM'))
        _this.RePwd.userId = user.uid
        console.log(user)
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.$ajax.post('/api/AdminModifyPwd',_this.RePwd).then(function(response){
              if(response.data.code === 0){
                _this.$message({message: response.data.data ,type: 'success'})
                _this.logout()
              }else{
                _this.$message({message: response.data.msg ,type: 'error'})
                _this.logout()
              }
            }).catch(function(error){console.log(error)})
          } else {
            return false
          }
        })
      },
      logout(){
        sessionStorage.removeItem('u')
        this.$router.push('/login')
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }
    }
  }
</script>

<style scoped>
.panel{
  width: 360px;
  margin-left: 20px;
  margin-bottom: 60px;
}
</style>



